import {
	mapMutations
} from 'vuex';

export default {
	data() {
		return {
			username: '',
			password: ''
		}
	},
	onLoad() {
		var userIdentity = uni.getStorageSync('userIdentity') || '';
		if (userIdentity) {
			this.username = userIdentity.username;
			this.password = userIdentity.password;
		}
	},
	methods: {
		...mapMutations(['setLoginInfo']),
		toLogin() {
			if (this.username.length < 5) {
				uni.showToast({
					icon: 'none',
					title: '账号最短为 5 个字符'
				});
				return;
			}
			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码最短为 6 个字符'
				});
				return;
			}
			uni.showLoading({
				title: '登录中',
				mask: false
			});
			global.$http.post('/core/login/doLogin', {
				params: {
					username: this.username,
					password: this.password
				},
			}).then(res => {
				uni.hideLoading();
				if (res.status === "0") {
					global.$http.post('/core/func/getUserAndMenu')
						.then(res => {
							if (res.status === "0") {
								uni.setStorageSync("userIdentity", {username: this.username, password: this.password});
								uni.setStorageSync("userInfo", res.data);
								this.setLoginInfo();
								uni.reLaunch({
									url: "/pages/index/home"
								});
							} else {
								uni.showToast({
									title: res.msg,
									icon: 'none'
								});
							}
						}).catch(err => {
							uni.showToast({
								title: err.message,
								icon: 'none'
						});
					});
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}
			}).catch(err => {
				uni.hideLoading();
				uni.showToast({
					title: err.message,
					icon: 'none'
				});
			});
		},
		toRegist(){
			uni.navigateTo({
				url: "register"
			});
		},
		findPassword(){
			uni.navigateTo({
				url:"findPassword"
			})
		}
	},

}
